body {
	line-height: 1;
  background-color:#131313;
  font-family: 'Rajdhani', -apple-system, BlinkMacSystemFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body:before {
  content: '';
  bottom: 0;
  position: absolute;
  z-index:-1;
  left:0;
  width: 100%;
  height: 30%;
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(19,19,19,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

}

body:after {
  content: '';
  background-color:#000000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(31, 31, 31, 0.4)),
    url(/images/bgtexture.png);
  background-attachment: fixed;
  background-repeat: repeat;
  z-index: -2;
  opacity: 0.45;
}


ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

