main {
  padding: 10px 30px 30px;
  max-width:1080px;
  margin: 0 auto;
}

@media screen and (min-width:1980px) {
  main {
    max-width: 1480px !important;
  }
}

@media screen and (max-width:1250px) {

  main {
    padding: 10px 70px 30px;
  }
}

@media screen and (max-width:680px) {

  main {
    padding: 10px 0 30px;
  }
}

